    },
    methods: {
        sortBy(prop) {
            this.projects.sort((a,b) => a[prop] < b[prop] ? -1 : 1)
        },
        remove_item(value) {
            const index = this.items.findIndex(item => item === value);
            this.items.splice(index, 1);
        }
    },
    created() {
        evntBus.$on('add_item',(item) => {
            item.qty = 1;
            item.vat = 18;
            item.price = 25;
            item.active = false,
            this.items.unshift(item);
        })
    },
